import s from "./lesara.module.css";
import React, {useState} from "react";
import {settings} from "../company/companyPage";

export const Lesara = () => {

    const [shake, setShake] = useState(false);

    const animate = (set: any) => {
        set(true);
        setTimeout(() => set(false), 1000);
    }

    return (
        <div className={s.yumpu_section}>
            <div className={s.lesara}>
                <div className={"slider_container"}>
                    <div className={s.yumpu_wrapper}>

                        <div className={s.company_data}>
                            <a className={[s.yumpu_logo, 'contrast'].join(" ")}>
                                <img {...settings}
                                     src={"https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d541dd7865d646ddafc0988_Lesara-Logo2016.png"}/>
                            </a>
                            <a {...settings} className={s.a_heading}><h1 className={s.yumpu_heading}>Lesara Mobile
                                Shop</h1></a>
                            <p {...settings} className={[s.yumpu_paragraph, s.tranc_text].join(" ")}>We’ve developed a
                                mobile retail app, which has been downloaded over 2M times. The rating of the new native
                                app has gone from 3.5 to 4.5 stars compared to the previous hybrid application.</p>
                            <div {...settings} className={s.yumpu_tags}>
                                <a href="#" className={[s.tag, s.tag_text].join(" ")}>Retail</a>
                                <a href="#" className={[s.tag, s.tag_text].join(" ")}>E-commerce</a>
                                <a href="#" className={[s.tag, s.tag_text].join(" ")}>iOS</a>
                                <a href="#" className={[s.tag, s.tag_text].join(" ")}>Android</a>
                            </div>
                            <div {...settings}>
                                <div onClick={() => animate(setShake)}
                                     className={[shake && "shake", "contrast", "company_button", s.lesara_button].join(" ")}>
                                    VIEW CASE STUDY
                                </div>
                            </div>
                        </div>
                        <div className={s.image_wrapper}>
                            <a className={s.img_block}><img {...settings} className={s.yumpu_img}
                                                            src={"https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d541d276e49614eed06ea30_lesara-1-p-500.png"}/></a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}