import React, {useEffect, useState} from 'react'
// import s from './header.module.css'
// @ts-ignore
import $ from 'jquery';
import './header.css'
import cn from "classnames";
import {NavLink} from 'react-router-dom'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faBars} from '@fortawesome/free-solid-svg-icons'

export const Header = ({page}:{page:string}) => {

    const [active, setActive] = useState(false)
    const [shakeCompany, setShakeCompany] = useState(false);
    const [shakeWork, setShakeWork] = useState(false);
    const [shakeCareers, setShakeCareers] = useState(false);
    const [shakeContact, setShakeContact] = useState(false);
    const [width, setWidth] = useState<any>($(window).width())

    const resize = () => {
        setWidth($(window).width())

    }

    useEffect(() => window.addEventListener('resize', resize), [])

    const animate = (set: any) => {

        set(true);
        setTimeout(() => set(false), 1000);
    }

    const handleEvent = (e:any) => e.target.classList.remove('current');

    return <div className={'navbar_x w-nav'}>
        <div className={cn({['active']: active}, 'container_x w-container')}>
            <a href="/" className="brand w-nav-brand w--current"><img alt={"temy-logo-simple"} className={'logo'}
                                                src={"https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5dbc29afc0a72841e8d7e045_temy-logo-simple.svg"}/></a>
            {width > 991 && (<nav className={cn({['active']: active}, 'nav-menu w-nav-menu',)}>
                <NavLink onMouseEnter={handleEvent} onClick={() => animate(setShakeCompany)}
                         className={[shakeCompany && "shake", 'nav-company w-nav-link', page === 'company' && 'current'].join(" ")} to="/">Company</NavLink>
                <NavLink onMouseEnter={handleEvent}  onClick={() => animate(setShakeWork)}
                         className={[shakeWork && "shake", 'nav-works w-nav-link', page === 'works' && 'current'].join(" ")} to="/">Work</NavLink>
                <NavLink onMouseEnter={handleEvent}  onClick={() => animate(setShakeCareers)}
                         className={[shakeCareers && "shake", 'nav-careers w-nav-link', page === 'careers' && 'current'].join(" ")} to="/">Careers</NavLink>
                <NavLink onMouseEnter={handleEvent} onClick={() => animate(setShakeContact)}
                         className={[shakeContact && "shake", 'nav-works contact-button w-nav-link', page === 'contact' && 'current'].join(" ")}
                         to="/contact">Contact us</NavLink>
            </nav>)}
            <div onClick={() => setActive(!active)} className={cn({['active']: active}, 'menu-button w-nav-button')}><FontAwesomeIcon
                icon={faBars}/></div>
            <div className={'clearfix'}/>
        </div>
        {width < 991 && (<nav className={cn({['active']: active}, 'nav-menu w-nav-menu collapsed')}>
            <NavLink onMouseEnter={handleEvent} onClick={() => animate(setShakeCompany)}
                     className={[shakeCompany && "shake", 'nav-company w-nav-link', page === 'company' && 'w--current'].join(" ")} to="/">Company</NavLink>
            <NavLink onMouseEnter={handleEvent}  onClick={() => animate(setShakeWork)}
                     className={[shakeWork && "shake", 'nav-works w-nav-link', page === 'works' && 'w--current'].join(" ")} to="/">Work</NavLink>
            <NavLink onMouseEnter={handleEvent}  onClick={() => animate(setShakeCareers)}
                     className={[shakeCareers && "shake", 'nav-careers w-nav-link', page === 'careers' && 'w--current'].join(" ")} to="/">Careers</NavLink>
            <NavLink onMouseEnter={handleEvent} onClick={() => animate(setShakeContact)}
                     className={[shakeContact && "shake", 'nav-works contact-button w-nav-link', page === 'contact' && 'w--current'].join(" ")}
                     to="/contact">Contact us</NavLink>
        </nav>)}
    </div>
}