import React, {useState} from 'react'
// import s from './reali.module.css'
import {settings} from "../company/companyPage";
import './reali.css'

export const Reali = () => {

    const [shake, setShake] = useState(false);

    const animate = (set: any) => {
        set(true);
        setTimeout(() => set(false), 1000);
    }

    return <div className="reali-section">
        <div className="reali-slider">
            <div className="slider-container">
                <div className="reali-col">
                    <div {...settings}
                         className="reali-image-container section-sizing">
                        <a href="https://www.temy.co/customers-collection/reali"
                           className="link-block-23 w-inline-block_x">
                            <img {...settings}
                                src="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d946ec77a88109c9aeb9d68_reali-mock.png"
                                sizes="(max-width: 479px) 92vw, (max-width: 767px) 94vw, 670.265625px"
                                srcSet="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d946ec77a88109c9aeb9d68_reali-mock-p-500.png 500w, https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d946ec77a88109c9aeb9d68_reali-mock-p-800.png 800w, https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d946ec77a88109c9aeb9d68_reali-mock-p-1080.png 1080w, https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d946ec77a88109c9aeb9d68_reali-mock.png 1488w"
                                alt="" className="image-36"/>
                        </a>
                    </div>
                    <div className="reali-content-container">
                        <div className="reali-content">
                            <img {...settings}
                                src="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d56b6a3d1dccfb36490a06f_8986c8bb-120e-49eb-b579-07d289156873-1546548100796.png"
                                 alt="" className="reali-log"/>
                            <a
                                data-w-id="1e01ab35-0757-2c35-99ce-9fcbe7992d33"
                                href="https://www.temy.co/customers-collection/reali"
                                className="link-block-25 w-inline-block_x">
                                <h1 {...settings} className="reali-heading"><strong className="bold-text">Reali Price
                                    Predictor</strong>
                                </h1>
                            </a>
                            <p {...settings}  className="reali-paragraph">
                                We developed a neural network that is trained to predict the prices of houses.
                            </p>
                            <div {...settings}
                                 className="tags-div-block tags-div-reali">
                                <a href="#" className="tags-home w-button">Real Estate</a>
                                <a href="#" className="tags-home w-button">Web</a>
                                <a href="#" className="tags-home w-button">Big Data</a>
                                <a href="#" className="tags-home w-button">Machine Learning</a>
                                <a href="#" className="tags-home w-button">AI</a>
                            </div>
                            <div {...settings}  className={["reali-button", 'contrast'].join(" ")}>
                                <div className="w-dyn-list">
                                    <div role="list" className="w-dyn-items">
                                        <div role="listitem" className="w-dyn-item">
                                            <a href="/customers-collection/reali"
                                               className="link-block-3 w-inline-block">
                                                <div className={["text-block-22"].join(" ")}>View Case Study</div>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}