import {Footer} from "../layout/footer/footer"
import {Header} from "../layout/header/header";
import './contactPage.css'
import main from '../company/companyPage.module.css'
import {Cookie} from "../cookie/cookie";
import {settings} from "../company/companyPage";

export const ContactPage = () => {

    return <div><Cookie/><Header page={'contact'}/>
        <div  className={'content'}>
            <div className={'body-container'}>
                <div className={'section-container'}>
                    <div className={'heading-container'}>
                        <div className={'heading_grid'}>

                            <div>
                                <div {...settings} className={'subtitle-bord'}>
                                    <h4 {...settings} className={main.heading_2}>
                                        SOFTWARE PRODUCT ENGINEERING<br/>
                                    </h4>
                                </div>
                            </div>
                            <h1 {...settings} className={[main.main_heading_1, main.grad_text].join(" ")}>Let's
                                create <br/> together</h1>
                        </div>
                    </div>
                    <div {...settings} className={'page-column-body w-row'}>
                        <div className={'column-17, w-col, w-col-2'}>
                        </div>
                        <div className={'column-37, w-col, w-col-10'}>
                            <div {...settings} className="w-row">
                                <div className="w-col w-col-9">
                                    <div className="w-form">
                                        <form id="email-form" name="email-form" data-name="Email Form"
                                              className="w-clearfix">
                                            <label {...settings} htmlFor="first-name" className="field-label-2">
                                                Name*
                                            </label>
                                            <input {...settings} type="text" className="text-field w-input" maxLength={256}
                                                   name="first-name"
                                                   data-name="first-name" id="first-name" required={true}/>
                                            <label {...settings} htmlFor="email-address" className="field-label">
                                                Email Address*
                                            </label>
                                            <input {...settings} type="email" className="text-field w-input" maxLength={256}
                                                   name="email-address"
                                                   data-name="email-address" id="email-address" required={true}/>
                                            <label
                                                htmlFor="company" className="field-label-3">
                                                Company</label>
                                            <input {...settings} type="text" className="text-field w-input" maxLength={256}
                                                   name="company" data-name="company" id="company"/>
                                            <label {...settings} htmlFor="message">
                                                <strong className="bold-text-2">
                                                    Tell us about your project*
                                                </strong>
                                            </label>
                                            <textarea {...settings} required={true} maxLength={5000} id="message" name="message"
                                                      data-name="message" className="textarea w-input"/>
                                            <label {...settings} className="w-checkbox checkbox_field">
                                                <input {...settings} type="checkbox" id="Send me NDA" name="Send-me-NDA"
                                                       data-name="Send me NDA" className="w-checkbox-input checkbox_x"/>
                                                <span className="checkbox_label w-form-label">Send me NDA</span>
                                            </label>
                                            <div {...settings} data-sitekey="6LdQ6cAUAAAAAA7KArGNK6jqy2CLjR-g81Zu52P6"
                                                 className="w-form-formrecaptcha recaptcha-2 g-recaptcha g-recaptcha-error g-recaptcha-disabled">
                                                <div {...settings} style={{width: '304px', height: '78px'}}>
                                                    <div>
                                                        <iframe title="reCAPTCHA"
                                                                src="https://www.google.com/recaptcha/api2/anchor?ar=1&amp;k=6LdQ6cAUAAAAAA7KArGNK6jqy2CLjR-g81Zu52P6&amp;co=aHR0cHM6Ly93d3cudGVteS5jbzo0NDM.&amp;hl=en&amp;v=FDTCuNjXhn1sV0lk31aK53uB&amp;size=normal&amp;cb=8qhw45a3ptpe"
                                                                width="304" height="78" role="presentation"
                                                                name="a-1fufdgdfkjwp" frameBorder="0" scrolling="no"
                                                                sandbox="allow-forms allow-popups allow-same-origin allow-scripts allow-top-navigation allow-modals allow-popups-to-escape-sandbox"/>
                                                    </div>
                                                    <textarea id="g-recaptcha-response" name="g-recaptcha-response"
                                                              className="g-recaptcha-response"
                                                              style={{
                                                                  width: 250,
                                                                  height: 40,
                                                                  border: '1px solid rgb(193, 193, 193)',
                                                                  margin: '10px 25px',
                                                                  padding: 0,
                                                                  resize: 'none',
                                                                  display: 'none'
                                                              }}/>
                                                </div>
                                                <iframe style={{display: 'none'}}></iframe>
                                            </div>
                                            <input type="submit" value="Send" data-wait="Please wait..."
                                                   className={["submit-button w-button", 'contrast'].join(" ")}/>
                                        </form>
                                        <div  className="w-form-done">
                                            <div>
                                                Thank you! Your submission has been received!
                                            </div>
                                        </div>
                                        <div  className="w-form-fail">
                                            <div>
                                                Oops! Something went wrong while submitting the form.
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="w-col w-col-3"/>
                                <div className="div-block-21">
                                    <h2>
                                        <strong {...settings} className="bold-text-3">
                                            London, United Kingdom
                                        </strong>
                                    </h2>
                                    <div  {...settings} className="text-block-4">
                                        3 Gower Street, London, United Kingdom, WC1E 6HA
                                        <a {...settings} href="tel:+380635730033">
                                            <br/>
                                        </a>
                                        hello@temy.co<br/><br/>Team Technologies Development Center LLP<br/><br/>Founded in 2014
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Footer/>
    </div>

}