import React, {useState} from "react";
import './footer.css'
import {settings} from "../../company/companyPage";

export const Footer = () => {

    const [shakeCompany, setShakeCompany] = useState(false);
    const [shakeWork, setShakeWork] = useState(false);
    const [shakeCareers, setShakeCareers] = useState(false);
    const [shakeContact, setShakeContact] = useState(false);

    const animate = (set:any) => {

        set(true);
        setTimeout(() => set(false), 1000);
    }

    return <div className="footer-section">
        <div className="bg-container">
            <div className="section-container footer-container">
                <a data-w-id="d266aa1e-0798-60b2-a304-efd2cbc17780" href="/" aria-current="page" className="link-block-6 w-inline-block w--current">
                    <img src="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5dbc29afc0a72841e8d7e045_temy-logo-simple.svg" alt="" className="logo"/>
                </a>
                <div  {...settings}  className="footer-navbar">
                    <div className="w-layout-grid_y grid-11_y">
                        <a  onClick = {() => animate(setShakeCompany)} className = {[shakeCompany && "shake", 'company-link w--current'].join(" ")} id="w-node-d266aa1e-0798-60b2-a304-efd2cbc17784-cbc1777d" href="/" aria-current="page">
                            Company
                        </a>
                        <a   onClick = {() => animate(setShakeWork)} className = {[shakeWork && "shake", 'works-link'].join(" ")} href="/works" id="w-node-d266aa1e-0798-60b2-a304-efd2cbc17786-cbc1777d">
                            Works
                        </a>
                        <a onClick = {() => animate(setShakeContact)} className = {[shakeContact && "shake", 'careers-link'].join(" ")} id="w-node-d266aa1e-0798-60b2-a304-efd2cbc1778a-cbc1777d" href="/careers">
                            Careers
                        </a>
                        <a onClick = {() => animate(setShakeCareers)} className = {[shakeCareers && "shake", 'contacts-link'].join(" ")} id="w-node-d266aa1e-0798-60b2-a304-efd2cbc17788-cbc1777d" href="/contact" >
                            Contact us
                        </a>

                    </div>
                </div>
                <div {...settings} className="div-block-24">
                    <div data-w-id="d266aa1e-0798-60b2-a304-efd2cbc1778d" className="div-block-18_y">
                        <div  {...settings}  className="html-embed w-embed w-script">
                            <script type="text/javascript" src="https://widget.clutch.co/static/js/widget.js">

                            </script>
                            <div className="clutch-widget" data-url="https://widget.clutch.co" data-widget-type="2" data-height="50" data-clutchcompany-id="81341">
                                <iframe id="iframe-0.9303424191801128" width="100%" src="https://widget.clutch.co/widgets/get/2?ref_domain=www.temy.co&amp;uid=81341&amp;ref_path=/" height="50px"/>
                            </div>
                        </div>
                    </div>
                    <div data-w-id="d266aa1e-0798-60b2-a304-efd2cbc1778f" className="div-block-19">
                        <a data-w-id="c9a22220-3a1d-f17a-5963-997627b5a1a1" href="https://www.youtube.com/channel/UC3pBIEYH1Vww9U-n19bwLyg/featured" target="_blank" className="w-inline-block" >
                            <img src="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d63c562a93ad99078d5957b_youtube.svg" width="32" height="32" data-w-id="d266aa1e-0798-60b2-a304-efd2cbc17790" alt="" className="youtube"/>
                        </a>
                        <a  href="https://medium.com/temy" target="_blank" className="w-inline-block">
                            <img src="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5d63c5625bf4d8af872b35ab_medium.svg" width="32" height="32" alt="" className="medium"/></a><a href="https://www.behance.net/temy-design" target="_blank" className="link-block-26 w-inline-block"><img src="https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5e8c9318915e52b2da25dd18_behance.svg" width="32" height="32" data-w-id="59789101-aedc-f02f-1a62-3cd41f465db3" alt=""/>
                    </a>
                    </div>
                    <div  data-w-id="d266aa1e-0798-60b2-a304-efd2cbc17792" className="div-block-16">
                        <div className="copyright_y">
                            © Team Technologies Development Center LLP - software development company, founded in the year 2014<br/>‍
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
}