import main from '../company/companyPage.module.css'
import {motion} from "framer-motion";
import {clients} from "../../store/state";
import React, {useEffect, useState} from "react";
import {settings} from "../company/companyPage";
import './hero.css'

export const Hero = () => {

    const text = [
        "Mobile Apps",
        "Cyber Security",
        "Cloud Solutions",
        "Software QA",
        "Internet of Things",
        "Product Design",
        "Big Data",
    ];

    const [shake, setShake] = useState(false);

    const animate = (set: any) => {

        set(true);
        setTimeout(() => set(false), 1000);
    }

    const [index, setIndex] = useState(0);

    useEffect(() => {
        let myInterval = setInterval(() => {
            if (index < 6) {
                setIndex(index + 1);
            } else setIndex(0)
        }, 4000)

        return () => {
            clearInterval(myInterval);
        };
    });
    return <div className={'hero-section'}>
        <div className={"body-container"}>
            <div className={"section-container"}>
                <div className="heading-container title-hero">
                    <div className="w-layout-grid grid-container">
                        <div>
                            <div {...settings}
                                style={{
                                    opacity: 1,
                                    transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)',
                                    transformStyle: 'preserve-3d',
                                    filter: 'contrast(100%)'
                                }}
                                className="subtitle-bord"><h4 {...settings} className="heading-2---temy">Software Development
                                Company</h4></div>
                        </div>
                        <div>
                            <div {...settings} className="div-block-62">
                                <h1
                                    style={{
                                        opacity: 1,
                                        transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)',
                                        transformStyle: 'preserve-3d',
                                        filter: 'contrast(100%)'
                                    }}
                                    className="main-heading-1">We deliver<br/></h1></div>
                            <div
                                style={{
                                    opacity: 1,
                                    transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)',
                                    transformStyle: 'preserve-3d',
                                }}
                                className="div-block-63 height_c">
                                <div {...settings} className={main.grad_wrapper}>{text.map((e, i) => index === i && <motion.h1
                                    {...settings}
                                    initial={{opacity: 0}}
                                    animate={{opacity: [0, 2, 0]}}
                                    transition={{delay: 0.4, duration: 2.6}}
                                    className={[main.grad_text, main.main_heading_1].join(" ")}>{e}
                                </motion.h1>)}</div>
                            </div>
                            <a {...settings}
                                style={{
                                    opacity: 1,
                                    transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)',
                                    transformStyle: 'preserve-3d',
                                }}
                                href="/contact" className={["contrast", "main-button w-inline-block"].join(" ")}>
                                <div className="text-block-15">GET IN TOUCH</div>
                            </a></div>
                    </div>
                </div>
                <div className="in-container clients">
                    <div {...settings} data-w-id="024cbd74-e1ee-e5e8-706d-19a134b021e2"
                         style={{
                             opacity: 1,
                             transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)',
                             transformStyle: 'preserve-3d',
                             filter: 'contrast(100%)'
                         }}
                         className="w-layout-grid grid-container grid-container-2">
                        <div className="div-block-102">
                            <div  {...settings}  className="subtitle-bord"><h4 className="heading-2---temy">Our Clients</h4></div>
                        </div>
                        <div id="w-node-_024cbd74-e1ee-e5e8-706d-19a134b021e4-08dd50f6">
                            <div className="collection-list-wrapper logos-home w-dyn-list">
                                <div className={"collection-list-5 w-dyn-items"}>
                                    {clients.map((e) => <div {...settings} role="listitem" className="collection-item w-dyn-item">
                                        <div className="div-block-23"><img
                                            src={e.url}
                                            alt={e.url.split("_")[1].split(".")[0]} className="image-23"/></div>
                                    </div>)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}