import './why_temy.css'
import {relevance} from "../../store/state";
import React from "react";
import {settings} from "../company/companyPage";

export const WhyTemy = () => {

    return <div className="why-temy-section">
        <div className="body-container">
            <div className="section-container">
                <div className="heading-container">
                    <div className="w-layout-grid grid-container">
                        <div>
                            <div {...settings} data-w-id="400f4a74-b2b0-1465-b663-0f878d2da12f"
                                 style={{opacity: 1, transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)', transformStyle: 'preserve-3d'}}
                                 className="subtitle-bord"><h4 className="heading-2---temy">YOUR TECH PARTNER</h4></div>
                        </div>
                        <div {...settings} data-w-id="400f4a74-b2b0-1465-b663-0f878d2da134"
                             style={{opacity: 1, transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)', transformStyle: 'preserve-3d'}}>
                            <h1 className="main-heading-1">Why Temy?<br/></h1></div>
                    </div>
                </div>
                <div className="in-container">
                    <div {...settings} className="w-layout-grid grid-container">
                        <div id="w-node-_400f4a74-b2b0-1465-b663-0f878d2da13a-08dd50f6"></div>
                        <div {...settings} className={'w-layout-grid grid-6'}>
                            {relevance.map((e) => <div {...settings} data-w-id="400f4a74-b2b0-1465-b663-0f878d2da157"
                                                       style={{opacity: 1, transform: 'translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg)', transformStyle: 'preserve-3d'}}
                                                       className="why-temy-item">
                                <div {...settings} className="div-block-50 w-clearfix"><img
                                    src={e.icon}
                                    alt="" className="image-11"/></div>
                                <div {...settings} className="div-block-49"><h1 className="heading-7">{e.title}</h1></div>
                            </div> ) }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
}