import {useState} from "react";
import './cookie.css'
export const Cookie = () => {
    const [active, setActive] = useState<boolean>(false)
    const [openDisclosure, setOpenDisclosure] = useState([false, false, false, false])

    const onClickHandle = () => {
        setActive(true)
    }
    const onOpenDisclosure = (id: number) => {
        let newDisclosure = openDisclosure
        newDisclosure[id] = true
        setOpenDisclosure(newDisclosure)
    }

    return <div className="osano-cm-window">
        <div role="dialog" aria-live="polite" aria-label="Consent Manager by Osano"
             aria-describedby="osano-id-cm-dialog" className="osano-cm-dialog osano-cm-dialog--hidden">
            <div className="osano-cm-dialog__content osano-cm-content">
                <span id="osano-id-cm-dialog" className="osano-cm-content__message osano-cm-message">This website stores data such as cookies to enable important site functionality including analytics, targeting, and personalization. You may alter your preferences at any time or accept the default settings.</span>
                <a href="https://www.temy.co/contact"
                   className="osano-cm-content__link osano-cm-link osano-cm-storage-policy" target="_blank">data storage
                    policy</a>
                <ul className="osano-cm-dialog-list">
                    <li className="osano-cm-dialog-list__item">

                        <label className="osano-cm-toggle" title="" htmlFor="ad7c3c60-d00f-4e52-911a-bbd32c1748ee">
                            <input className="osano-cm-toggle__checkbox" data-category="MARKETING"
                                   id="ad7c3c60-d00f-4e52-911a-bbd32c1748ee" type="checkbox"/>
                                <span className="osano-cm-toggle__switch"/>
                                <span className="osano-cm-toggle__label osano-cm-label">Marketing</span>
                        </label>
                    </li>
                    <li className="osano-cm-dialog-list__item">

                        <label className="osano-cm-toggle" title="" htmlFor="f75be636-1445-4304-96f7-44304d2892ff">
                            <input className="osano-cm-toggle__checkbox" data-category="PERSONALIZATION"
                                   id="f75be636-1445-4304-96f7-44304d2892ff" type="checkbox"/>
                                <span className="osano-cm-toggle__switch"/>
                                <span className="osano-cm-toggle__label osano-cm-label">Personalization</span>
                        </label>
                    </li>
                    <li className="osano-cm-dialog-list__item">

                        <label className="osano-cm-toggle" title="" htmlFor="25223114-ba7e-4d63-94d2-6f58031d2385">
                            <input className="osano-cm-toggle__checkbox" data-category="ANALYTICS"
                                   id="25223114-ba7e-4d63-94d2-6f58031d2385" type="checkbox"/>
                                <span className="osano-cm-toggle__switch"/>
                                <span className="osano-cm-toggle__label osano-cm-label">Analytics</span>
                        </label>

                    </li>
                </ul>

            </div>
            <div className="osano-cm-buttons">
                <button className="osano-cm-button osano-cm-save">Save</button>
                <button className="osano-cm-button osano-cm-accept-all">Accept All</button>
            </div>
        </div>
        <button onClick={() => setActive(true)} className={[active ? 'osano-cm-widget osano-cm-widget--hidden' :'osano-cm-widget'].join(" ")} title="Storage Preferences" aria-label="Storage Preferences">
            <svg onClick={onClickHandle} width="40" height="40" viewBox="0 0 71.85 72.23" xmlns="http://www.w3.org/2000/svg">
                <path
    d="m67.6 36.73a6.26 6.26 0 0 1 -3.2-2.8 5.86 5.86 0 0 0 -5.2-3.1h-.3a11 11 0 0 1 -11.4-9.5 6 6 0 0 1 -.1-1.4 9.2 9.2 0 0 1 .4-2.9 8.65 8.65 0 0 0 .2-1.6 5.38 5.38 0 0 0 -1.9-4.3 7.3 7.3 0 0 1 -2.5-5.5 3.91 3.91 0 0 0 -3.5-3.9 36.46 36.46 0 0 0 -15 1.5 33.14 33.14 0 0 0 -22.1 22.7 35.62 35.62 0 0 0 -1.5 10.2 34.07 34.07 0 0 0 4.8 17.6.75.75 0 0 0 .07.12c.11.17 1.22 1.39 2.68 3-.36.47 5.18 6.16 5.65 6.52a34.62 34.62 0 0 0 55.6-21.9 4.38 4.38 0 0 0 -2.7-4.74z"
    fill="#fff" stroke="#29246a" stroke-width="3"/>
                <path
    d="m68 41.13a32.37 32.37 0 0 1 -52 20.5l-2-1.56c-2.5-3.28-5.62-7.15-5.81-7.44a32 32 0 0 1 -4.5-16.5 34.3 34.3 0 0 1 1.4-9.6 30.56 30.56 0 0 1 20.61-21.13 33.51 33.51 0 0 1 14.1-1.4 1.83 1.83 0 0 1 1.6 1.8 9.38 9.38 0 0 0 3.3 7.1 3.36 3.36 0 0 1 1.2 2.6 3.37 3.37 0 0 1 -.1 1 12.66 12.66 0 0 0 -.5 3.4 9.65 9.65 0 0 0 .1 1.7 13 13 0 0 0 10.5 11.2 16.05 16.05 0 0 0 3.1.2 3.84 3.84 0 0 1 3.5 2 10 10 0 0 0 4.1 3.83 2 2 0 0 1 1.4 2z"
    fill="#fff" stroke="#29246a" stroke-width="3"/>
                <g fill="#37cd8f">
                    <path d="m26.6 31.43a5.4 5.4 0 1 1 5.4-5.43 5.38 5.38 0 0 1 -5.33 5.43z"/>
                    <path d="m25.2 53.13a5.4 5.4 0 1 1 5.4-5.4 5.44 5.44 0 0 1 -5.4 5.4z"/>
                    <path d="m47.9 52.33a5.4 5.4 0 1 1 5.4-5.4 5.32 5.32 0 0 1 -5.24 5.4z"/>
                </g>
            </svg>
        </button>
        <div className={[active ? 'osano-cm-info-dialog-wrapper' : "osano-cm-info-dialog-wrapper osano-cm-info-dialog-wrapper--hidden"].join(" ")}>
            <div className={[active ? 'osano-cm-info-dialog osano-cm-info-dialog--open' : "osano-cm-info-dialog"].join(" ")}>
                <div className="osano-cm-info-dialog__header">
                    <button className="osano-cm-info-dialog-close" >
                        <svg onClick={() => setActive(false)} aria-role="button" width="20px" height="20px" xmlns="http://www.w3.org/2000/svg"
                             xmlnsXlink="http://www.w3.org/1999/xlink" viewBox="0 0 25 25">
                            <title>Close Icon</title>
                            <polygon className="x"
    points="25 0.71 24.29 0 12.5 11.79 0.71 0 0 0.71 11.79 12.5 0 24.29 0.71 25 12.5 13.21 24.29 25 25 24.29 13.21 12.5 25 0.71"/>
                        </svg>
                    </button>
                    <p className="osano-cm-info-dialog__header-title">Storage Preferences</p>
                    <p className="osano-cm-info-dialog__header-description">When you visit web sites, they may store or
                        retrieve data in your web browser. This storage is often necessary for basic functionality of
                        the web site or the storage may be used for the purposes of marketing, analytics, and
                        personalization of the web site such as storing your preferences. Privacy is important to us so
                        you have the option of disabling certain types of storage that may not be necessary to the basic
                        functioning of the web site. Blocking categories may impact your experience on the web site.</p>
                </div>
                <ul className="osano-cm-info-list">
                    <li className="osano-cm-info-list__item">

                        <label className="osano-cm-toggle" title="Locked: Please read description."
                               htmlFor="27250bd2-6ae0-4c2f-81ae-dfda210d0421">
                            <input className="osano-cm-toggle__checkbox" data-category="ESSENTIAL"
                                   id="27250bd2-6ae0-4c2f-81ae-dfda210d0421" type="checkbox" checked={true} disabled={true}/>
                                <span className="osano-cm-toggle__switch"/>
                                <span className="osano-cm-toggle__label osano-cm-label">Strictly Necessary</span>
                        </label>

                        <div className="osano-cm-description osano-cm-info-list__item-description">
                            Essential in order to enable you to navigate the website and use its features, such as
                            accessing secure areas of the website.
                        </div>
                        <details onClick={() => onOpenDisclosure(0)} data-section="ESSENTIAL" className={[ openDisclosure[0] ? "osano-cm-disclosure osano-cm-disclosure--loaded" : "osano-cm-disclosure osano-cm-disclosure--collapse"].join(" ")}>
                            <summary className="osano-cm-disclosure__toggle">Disclosure</summary>
                            <ul data-type="cookies" className="osano-cm-disclosure__list">
                                <li className="osano-cm-disclosure__list-item">No Disclosures</li>
                            </ul>

                        </details>
                    </li>
                    <li className="osano-cm-info-list__item">

                        <label className="osano-cm-toggle" title="" htmlFor="1df5697c-082e-4deb-8d5e-fd5b1524ee93">
                            <input className="osano-cm-toggle__checkbox" data-category="MARKETING"
                                   id="1df5697c-082e-4deb-8d5e-fd5b1524ee93" type="checkbox" />
                                <span className="osano-cm-toggle__switch"/>

                        </label>

                        <div className="osano-cm-description osano-cm-info-list__item-description">
                            Used to deliver advertising that is more relevant to you and your interests. They are also
                            used to limit the number of times you see an advertisement as well as help measure the
                            effectiveness of the advertising campaign. They are usually placed by advertising networks
                            with the website operator’s permission.
                        </div>
                        <details onClick={() => onOpenDisclosure(1)} data-section="ESSENTIAL" className={[ openDisclosure[1] ? "osano-cm-disclosure osano-cm-disclosure--loaded" : "osano-cm-disclosure osano-cm-disclosure--collapse"].join(" ")}>
                            <summary className="osano-cm-disclosure__toggle">Disclosure</summary>
                            <ul data-type="cookies" className="osano-cm-disclosure__list">
                                <li className="osano-cm-disclosure__list-item">No Disclosures</li>
                            </ul>

                        </details>
                    </li>
                    <li className="osano-cm-info-list__item">

                        <label className="osano-cm-toggle" title="" htmlFor="58150f74-edbb-4591-83e2-b3ffaeeee587">
                            <input className="osano-cm-toggle__checkbox" data-category="PERSONALIZATION"
                                   id="58150f74-edbb-4591-83e2-b3ffaeeee587" type="checkbox"/>
                                <span className="osano-cm-toggle__switch"/>
                                <span className="osano-cm-toggle__label osano-cm-label">Personalization</span>
                        </label>

                        <div className="osano-cm-description osano-cm-info-list__item-description">
                            Allows the website to remember choices you make (such as your user name, language or the
                            region you are in) and provide enhanced, more personal features. For example, a website may
                            be able to provide you with local weather reports or traffic news by storing data about the
                            region in which you are currently located.
                        </div>
                        <details onClick={() => onOpenDisclosure(2)} data-section="ESSENTIAL" className={[ openDisclosure[2] ? "osano-cm-disclosure osano-cm-disclosure--loaded" : "osano-cm-disclosure osano-cm-disclosure--collapse"].join(" ")}>
                            <summary className="osano-cm-disclosure__toggle">Disclosure</summary>
                            <ul data-type="cookies" className="osano-cm-disclosure__list">
                                <li className="osano-cm-disclosure__list-item">No Disclosures</li>
                            </ul>

                        </details>
                    </li>
                    <li className="osano-cm-info-list__item">

                        <label className="osano-cm-toggle" title="" htmlFor="acbf3255-9c18-4391-bce3-3c7fccbb8b65">
                            <input className="osano-cm-toggle__checkbox" data-category="ANALYTICS"
                                   id="acbf3255-9c18-4391-bce3-3c7fccbb8b65" type="checkbox"/>
                                <span className="osano-cm-toggle__switch"/>
                                <span className="osano-cm-toggle__label osano-cm-label">Analytics</span>
                        </label>

                        <div className="osano-cm-description osano-cm-info-list__item-description">

                            Provides information about how a web site performs, how each page renders, and whether there
                            are technical issues on the web site to the web site operator. This storage type generally
                            doesn’t collect information that identifies a visitor.
                        </div>
                        <details onClick={() => onOpenDisclosure(3)} data-section="ESSENTIAL" className={[ openDisclosure[0] ? "osano-cm-disclosure osano-cm-disclosure--loaded" : "osano-cm-disclosure osano-cm-disclosure--collapse"].join(" ")}>
                            <summary className="osano-cm-disclosure__toggle">Disclosure</summary>
                            <ul data-type="cookies" className="osano-cm-disclosure__list">
                                <li className="osano-cm-disclosure__list-item">No Disclosures</li>
                            </ul>

                        </details>
                    </li>

                </ul>
                <button onClick={() => setActive(false)} aria-role="button" className="osano-cm-info__button osano-cm-button osano-cm-save">Save</button>
                <div className="osano-cm-powered-by">
                    <a className="osano-cm-link osano-cm-powered-by__link"
                       href="https://www.osano.com/?utm_campaign=cmp&amp;utm_source=cmp-dialog&amp;utm_medium=drawer">Powered
                        By Osano</a>
                </div>
            </div>
        </div>
    </div>
}